import ct from "@/utils/constant/content_type";

class Api {
    constructor(uri, way, contentType=ct.APPLICATION) {
        this.uri = uri;
        this.way = way;
        this.contentType = contentType;
    }
}

/**
 * 接口
 */
export default {

    /** -- 文件接口 -- **/

    // 上传文件
    uploadFile: new Api("/api/file", "POST", ct.MULTIPART),

    // 根据 ID 删除文件
    deleteFile: new Api("/api/file/{id}", "DELETE"),

    // 删除文件列表
    deleteFileList: new Api("/api/file", "DELETE"),

    // 显示图片
    getImg: new Api("/api/img/{id}", "GET"),

    /** -- 文章接口 -- **/

    // 获取文章详情
    getArticle: new Api("/api/article/{id}", "GET"),

    // 提交文章 发布/保存草稿
    release: new Api("/api/article", "POST"),

    // 更新文章
    updateArticle: new Api(("/api/article"), "PATCH"),

    // 查询文章列表
    getArticleList: new Api("/api/article", "GET"),

    /** -- 用户接口 -- **/

    // 用户注册
    register: new Api("/api/user", "POST"),

    // 用户信息更新
    userInfoUpdate: new Api("/api/user", "PATCH")
}
