import "./theme/index.css";
import { Row, Col, Button, Autocomplete, Radio, Dropdown, DropdownMenu, DropdownItem,
    Notification, Alert, Backtop, Card } from "element-ui";

Vue.use(ElementUI);

Vue.component(Row.name, Row);
