import Vue from 'vue';
import Router from 'vue-router';
import view from "./view";
import App from "@/App";
import state from "@/store/state";

Vue.use(Router);

let router = new Router({
    mode: process.env.ROUTER_MODE,
    base: process.env.BASE_URL,
    routes: [{
                path: "/",
                name: "home",
                component: view.home,
                meta: {
                    keepAlive: true
                }
            },{
                path: "/resume",
                name: "resume",
                component: view.resume,
                meta: {
                    keepAlive: true
                }
            },{
                path: "/cheditor",
                name: "cheditor",
                component: view.cheditor,
                meta: {
                    keepAlive: true
                }
            },{
                path: "/bookshelf",
                name: "bookshelf",
                component: view.bookshelf,
                meta: {
                    keepAlive: false
                }
            },{
                path: "/blog",
                name: "blog",
                component: view.blog,
                meta: {
                    keepAlive: false
                }
            },{
                path: "/blog/detail",
                name: "blogDetail",
                component: view.blogDetail,
                meta: {
                    keepAlive: false
                }
            }
        ]
});

/**
 * 切换路由
 */
router.beforeEach((to, from, next) => {
    state.currentPage = to.fullPath;
    next();
});

export default router;